<template>
  <div class="footer__header">
    <div class="container">
      <button class="footer__input1">На неделю</button>
      <button class="footer__input">Отменить</button>
    </div>
  </div>
  <footer>
    <div class="footer__cards">
      <div class="container">
        <div
          class="footer__card-rd"
          v-for="(day, index) in getDailyWeather"
          :key="index"
        >
          <div class="footer__card">
            <div class="footer__card-texts">
              <h1 class="footer__card-title">{{ getWeekDay(day) }}</h1>
              <p class="footer__date">{{ getDay(day) }} {{ getMonth(day) }}</p>
              <img
                :src="`${require(`/src/assets/images/${day.weather[0].main}.svg`)}`"
                alt=""
                class="footer__img"
              />
            </div>
            <div class="footer__card-botom">
              <h1 class="footer__card-title">
                {{ Math.round(day.temp.max) }}°
              </h1>
              <p class="footer__degree">
                {{ Math.round(day.feels_like.day) }}°
              </p>
              <p class="footer__weather-text">
                {{ day.weather[0].description }}
              </p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </footer>
</template>


<script>
import unix from "@/timestamp";
import { mapGetters } from "vuex";

export default {
  data() {
    return {};
  },
  computed: {
    ...mapGetters(["getDailyWeather"]),
    getWeekDay: () => (day) => {
      return unix(day.dt, "weekday");
    },
    getDay: () => (day) => {
      return unix(day.dt, "day");
    },
    getMonth: () => (day) => {
      return unix(day.dt, "month");
    },
  },
};
</script>

